import React, { Component } from 'react';
import {
	ComposableMap,
	ZoomableGroup,
	Geographies,
	Geography,
	Markers,
	Marker,
} from 'react-simple-maps';
import { Motion, spring } from 'react-motion';

import './Map.css';

const members = [
	//                                                Long , Lat
	{ name: 'Natty', place: 'Bangkok', coordinates: [100.5018, 13.7563] },
	{ name: 'Odai', place: 'Ibb', coordinates: [44.1625, 13.9721] },
	{ name: 'Omair', place: 'Mississauga', coordinates: [-79.6441, 43.5890] },
	{ name: 'Tanner', place: 'Calgary', coordinates: [-114.0708, 51.0486] },
];

class Map extends Component {
	constructor() {
		super();
		this.state = {
			center: [0,20],
			zoom: 1,
		};
		this.handleZoomIn = this.handleZoomIn.bind(this);
		this.handleZoomOut = this.handleZoomOut.bind(this);
		this.handleMemberClick = this.handleMemberClick.bind(this);
		this.handleMemberSelection = this.handleMemberSelection.bind(this);
		this.handleReset = this.handleReset.bind(this);
	}

	handleZoomIn() {
		this.setState({
			zoom: this.state.zoom * 2,
		});
	}

	handleZoomOut() {
		this.setState({
			zoom: this.state.zoom / 2,
		});
	}

	handleMemberClick(member) {
		this.setState({
			zoom: 4,
			center: member.coordinates,
		});
	}

	handleMemberSelection(evt) {
		const memberId = evt.target.getAttribute('data-member')
		const member = members[memberId]
		this.setState({
			center: member.coordinates,
			zoom: 4,
		})
	}

	handleReset() {
		this.setState({
			center: [0,20],
			zoom: 1,
		});
	}

	render() {
		return (
			<div>
				<h5>Kopjes Map</h5>
				<Motion
					defaultStyle={{
						zoom: 1,
						x: 0,
						y: 20,
					}}
					style={{
						zoom: spring(this.state.zoom, {stiffness: 210, damping: 20}),
						x: spring(this.state.center[0], {stiffness: 210, damping: 20}),
						y: spring(this.state.center[1], {stiffness: 210, damping: 20}),
					}}
					>
					{({zoom,x,y}) => (
						<ComposableMap
							projectionConfig={{ scale: 205 }}
							width={980}
							height={551}
							style={{
								width: '100%',
								height: 'auto',
							}}
							>
							<ZoomableGroup center={[x,y]} zoom={zoom}>
								<Geographies geography='/world-50m.json'>
									{(geographies, projection) =>
										geographies.map((geography, i) => geography.id !== '010' && (
											<Geography
												key={i}
												geography={geography}
												projection={projection}
												style={{
													default: {
														fill: '#ECEFF1',
														stroke: '#607D8B',
														strokeWidth: 0.75,
														outline: 'none',
													},
													hover: {
														fill: '#CFD8DC',
														stroke: '#607D8B',
														strokeWidth: 0.75,
														outline: 'none',
													},
													pressed: {
														fill: '#FF5722',
														stroke: '#607D8B',
														strokeWidth: 0.75,
														outline: 'none',
													},
												}}
											/>
									))}
								</Geographies>
								<Markers>
									{members.map((member, i) => (
										<Marker
											key={i}
											marker={member}
											onClick={this.handleMemberClick}
											>
											<circle
												cx={0}
												cy={0}
												r={6}
												fill='#FF5722'
												stroke='#DF3702'
											/>
										</Marker>
									))}
								</Markers>
							</ZoomableGroup>
						</ComposableMap>
					)}
				</Motion>
				<div className='map-controls row'>
					<div className='one-third column'>
						<button onClick={this.handleZoomIn}>Zoom In</button>
					</div>
					<div className='one-third column'>
						<button onClick={this.handleZoomOut}>Zoom Out</button>
					</div>
					<div className='one-third column'>
						<button onClick={this.handleReset}>Reset</button>
					</div>
				</div>
				<h6>Kopjes Members</h6>
				{
					members.map((member, i) => (
						<div key={i} className='map-member'>
							<a
								href='javascript:void(0)'
								data-member={i}
								onClick={this.handleMemberSelection}
							>
								{member.name}
							</a>
						</div>
					))
				}
			</div>
		)
	}
}

export default Map;



